/** @jsxRuntime classic */
/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { useContext } from 'react';
import { MovieContext } from '../../Context/MovieContext';

const HeroNavLink = ({btnText}) => {
  const {setHiddenMenu, activateLink, setActiveLink} = useContext(MovieContext);


  
  return(
    <button onClick ={() => {
      setActiveLink(btnText);
      setHiddenMenu(true);
    }} 
    css = {styles}
    style = {{color: activateLink === btnText ? "#DC234D" : "#fff"}}
    
    >{btnText}
    </button>
    );
};

const styles = css`
margin-left: 15px;
margin-right: 5px;
border: none;
outline: none;
background: transparent;
font-size: 22px;
font-weight: 700;
cursor: pointer;
user-select: none;
@media(max-width: 860px){
  font-size:40px;

}

`;


export default HeroNavLink;